import { Component, OnInit } from '@angular/core';
import { PacienteService } from 'src/app/services/paciente.service';
import { Paciente } from 'src/app/models/paciente';
import { NavExtrasService } from 'src/app/services/nav-extras.service';
import { Router } from '@angular/router';
import { Events } from '@ionic/angular';

@Component({
  selector: 'app-pacientes',
  templateUrl: './pacientes.page.html',
  styleUrls: ['./pacientes.page.scss'],
})
export class PacientesPage implements OnInit {

  pacientes: Paciente[] = []
  
  constructor(private pacientesService:PacienteService, private navExtra:NavExtrasService, private router:Router, private events: Events) { }

  async ngOnInit() {
    this.atualizarConteudos();
    this.events.subscribe('pacientes:atualizar', (atualizar) => {
      if (atualizar) this.atualizarConteudos();
    })
  }
  

  /**
   * Abre a visualização para um paciente
   */
  visualizar(paciente:Paciente) {
    this.navExtra.set('paciente', paciente);
    this.router.navigateByUrl('/coordenador/pacientes/visualizar')
  }

  /**
   * Atualiza a lista caso desejado
   * @param event 
   */
  async refresh(event) {
    await this.atualizarConteudos();
    event.target.complete();
  }

  /**
   * Atualiza a lista 
   */
  async atualizarConteudos(){
    this.pacientes = await this.pacientesService.buscarTodos();
    console.log('a')
  }

}
